<template>
    <div v-if="ready">
        <div class='col-12' style="display:flex;flex-direction:row;">
            <div class='col-3' style='padding-left:0;margin-bottom:1em'>
                <label>Department</label>
                <select class='form-control select' v-model="dept" @change="new_data">
                    <option v-for="d in dept_list" :value="d.dept_id">{{ d.full_dept }}</option>
                </select>
            </div>
            <div class='col-1'></div>
            <div class='col-2'>
                <label>Start Date</label>
                <input type="date" v-model="st" class="form-control" @change="new_data"/>
            </div>
            <div class='col-2'>
                <label>End Date</label>
                <input type="date" v-model="ed" class="form-control" @change="new_data"/>
            </div>
            <div class="col-4" style='padding-right:0'>
                <label>Live Search</label>
                <input type="text" class="form-control" placeholder="Enter Search Key" v-model="key" @keyup="live_new_data"/>
            </div>
        </div>
        <div class='col-12' v-html="print_list" v-if="list_ready">
            {{ print_list }}
        </div>
        <input type="hidden" id='trigger_view_reimb_recur' @click="view_reimb"/>
        <input type="hidden" id='trigger_view_proc' @click="view_proc_records"/>       
    </div>    
</template>
<script>
export default {
    data(){
        return{
            list_ready:false,
            key:'',
            st:'',
            ed:'',
            dept:'',
            dept_list:[],
            ready:false,
            print_list:'',
        }
    },
async created(){
    await axios.get('Procurement/shared_backend/department.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].dept_id;
            }
        )
    var tdy = new Date();
    var month = tdy.getMonth();
    month = month+1;
    if(month>7){
        this.st = tdy.getFullYear()+'-08-01';
        this.ed = (tdy.getFullYear()+1)+'-07-31';
    } else {
        this.st = (tdy.getFullYear()-1)+'-08-01';
        this.ed = tdy.getFullYear()+'-07-31';
    }
    var formData = new FormData();
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    await axios.post('Procurement/report/backend/reimb_fetch.php',formData)
        .then(
            (response)=>{
                this.print_list = response.data;
            }
        ) 
this.ready = true;
this.list_ready=true; 
},
methods:{
    view_reimb(){
        var tag = document.getElementById('tmp_id').value;
        var st_val = this.st;
        var st_array = st_val.split('-');
        var st_month = st_array[0]+'-'+st_array[1];
        document.getElementById('st_tmp').value = st_month;

        var ed_val = this.ed;
        var ed_array = ed_val.split('-');
        var ed_month = ed_array[0]+'-'+ed_array[1];
        document.getElementById('ed_tmp').value = ed_month;
        document.getElementById('doc_type').value = 'Reimbursement';
        this.$router.push('../wrapper/form_view');
    },
    new_data(){
    var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        axios.post('Procurement/report/backend/reimb_fetch.php',formData)
            .then(
                (response)=>{
                    this.print_list = response.data;
                    this.key='';
                }
            )  
    },
    async live_new_data(){
        this.list_ready=false;
        var formData = new FormData();
            formData.append('key',this.key);
            formData.append('dept',this.dept);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            await axios.post('Procurement/report/backend/reimb_fetch.php',formData)
                .then(
                    (response)=>{
                        this.print_list = response.data;
                    }
                ) 
        this.list_ready=true;  
    },
    view_reimb_records(){
        this.$router.push('../reimbursement/view_record');
    },
    view_proc_records(){
        this.$router.push('../procurement/view_proc');
    }
}
}
</script>



